import React from "react";
import { graphql } from "gatsby";

import { Layout } from "../components/common";
import SEO from "../components/seo";
// import { getShareButton } from "../components/Careers/PositionCard/positionCard";
import {
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";

import {
    FacebookShareButton,
    TwitterShareButton,
    TelegramShareButton,
    WhatsappShareButton,
} from "react-share";

export const getShareButton = (job, dropdownOpen, toggle) => (
    <Dropdown isOpen={dropdownOpen} toggle={toggle} className="pr-5">
        <DropdownToggle caret className="btn btn-success  text-left ">
            <i className="fa fa-share-alt" /> &nbsp; Share Job
        </DropdownToggle>
        {typeof window !== `undefined` && job !== undefined && (
            <DropdownMenu bottom>
                <DropdownItem>
                    <FacebookShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn  btn-block b-0 text-primary bg-white">
                            <i className=" fa fa-facebook-f " />
                            &nbsp;&nbsp;Facebook
                        </button>
                    </FacebookShareButton>
                </DropdownItem>
                <DropdownItem>
                    <TwitterShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn o btn-block text-info bg-white">
                            <i className=" fa fa-twitter " />
                            &nbsp;&nbsp;Twitter
                        </button>
                    </TwitterShareButton>
                </DropdownItem>
                <DropdownItem>
                    <WhatsappShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn  btn-block text-success bg-white">
                            <i className=" fa fa-whatsapp " />
                            &nbsp;&nbsp;Whatsapp
                        </button>
                    </WhatsappShareButton>
                </DropdownItem>
                <DropdownItem>
                    <TelegramShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none  dropdown-item text-left btn  btn-block text-info bg-white">
                            <i className=" fa fa-telegram " />
                            &nbsp;&nbsp;Telegram
                        </button>
                    </TelegramShareButton>
                </DropdownItem>
            </DropdownMenu>
        )}
    </Dropdown>
);

class Job extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            firstName: ``,
            lastName: ``,
            email: ``,
            phone: ``,
            loaded: ``,
            experience: ``,
            resume: ``,
            location: ``,
            githubLink: ``,
            sourceFrom: ``,
            fileLabel: `Please upload your resume`,
            loading: false,
            error: false,
            errorMessage: ``,
            visible: true,
            sharedropdownOpen: false,
        };

        this.handleChange = this.handleChange.bind(this);
        this.onFileChange = this.onFileChange.bind(this);
    }

    handleChange(evt) {
        // var validated = false;
        this.setState({
            [evt.target.name]: evt.target.value,
        });

        // if (evt.target.checkValidity() === false) {
        //     evt.target.classList.add('error-invalid');
        //     evt.target.nextElementSibling.classList.add('d-block');
        //     validated = false;
        // } else {
        //     evt.target.classList.remove('error-invalid');
        //     evt.target.nextElementSibling.classList.remove('d-block');
        //     validated = true;
        // }
    }

    handleSubmit = (e) => {
        e.preventDefault();

        e.preventDefault();

        var validated = false;
        var forms = document.getElementsByClassName(`needs-validation`);
        Array.prototype.filter.call(forms, function (form) {
            if (form.checkValidity() === false) {
                e.preventDefault();
                e.stopPropagation();

                form.classList.add(`was-validated`);
                validated = false;
            } else {
                form.classList.add(`was-validated`);
                validated = true;
            }
        });
        if (validated) {
            this.setState({ loading: true });

            var payload = {
                fields: [
                    {
                        key: `candidate_first_name`,
                        value: this.state.firstName,
                    },
                    { key: `candidate_last_name`, value: this.state.lastName },
                    { key: `candidate_email`, value: this.state.email },
                    { key: `candidate_phone`, value: this.state.phone },
                    {
                        key: `how_did_you_hear_about_us`,
                        value: this.state.sourceFrom,
                    },
                    { key: `preferred_location`, value: this.state.location },
                    { key: `github_profile`, value: this.state.githubLink },
                    { key: `expected_salary`, value: this.state.experience },
                    {
                        key: `resume`,
                        value: {
                            encoded_data: this.state.resume
                                .split(`,`)
                                .slice(-1)[0],
                            file_name: this.state.fileLabel,
                        },
                    },
                ],
                source: this.props.location.state.referer,
            };
        }
    };

    onFileChange(e, file) {
        var fileData = file || e.target.files[0],
            reader = new FileReader();

        this.setState({ loaded: false });
        this.setState({ fileLabel: e.target.files[0].name });

        reader.onload = (e) => {
            this.setState({
                resume: reader.result,
                loaded: true,
            });
        };

        reader.readAsDataURL(fileData);
    }
    toggleShare = () => {
        this.setState({ sharedropdownOpen: !this.state.sharedropdownOpen });
    };
    render() {
        const {
            description,
            lists,
            categories,
            text,
            descriptionPlain,
            id,
            applyUrl,
        } = this.props.data.lever;

        return (
            <Layout location={this.props.location}>
                <SEO title={text} description={descriptionPlain} />
                <SEO
                    isDynamic={false}
                    title={`Gojek Jobs | ${text}`}
                    description={descriptionPlain}
                    url="https://gojek.io"
                    image="https://www.gojek.io/images/careers/careers-banner/career-banner-1.jpg"
                />
                <div className="container first-section">
                    <div className="d-flex flex-row flex-wrap pb-5 mt-5">
                        <div
                            id={id}
                            name={id}
                            className="col-md-12  mt-5  col-12 col-md-6 mt-4 mb-0 px-2"
                        >
                            <div className=" scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight border-success pt-3">
                                <h6 className=" px-4 roboto-regular font-sm text-dark ">
                                    {text}
                                </h6>
                                <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                                    <span className="pl-4  text-green col-">
                                        {categories.location}
                                    </span>
                                    <span className="pl-4  text-black col-">
                                        {categories.team}
                                    </span>
                                </div>
                                <div>
                                    <div className="pl-4 pt-3">
                                        <div className="row justify-content-between align-items-center pl-2">
                                            <a
                                                rel="noopener noreferrer"
                                                href={applyUrl}
                                                target="_blank"
                                                className="apply btn btn-success px-5 apply  my-2"
                                            >
                                                APPLY NOW
                                            </a>
                                            <div className="d-block">
                                                {/* {getShareButton(
                                                    this.props.data.lever
                                                )} */}
                                                {getShareButton(
                                                    this.props.data.lever,
                                                    this.state
                                                        .sharedropdownOpen,
                                                    this.toggleShare
                                                )}
                                            </div>
                                        </div>
                                        <div className="py-5 my-5 py-md-2 my-md-0">
                                            <p className="  roboto-bold text-black font-sm">
                                                Overview
                                            </p>
                                            <div className=" description-font text-dark col-12 pl-1">
                                                <div
                                                    dangerouslySetInnerHTML={{
                                                        __html: description,
                                                    }}
                                                />
                                            </div>
                                            <div className="">
                                                {lists.map((list, i) => (
                                                    <React.Fragment>
                                                        <p className="pt-3 roboto-bold text-black font-sm">
                                                            {list.text}
                                                        </p>
                                                        <ul className="pl-3 mb-3">
                                                            <div
                                                                dangerouslySetInnerHTML={{
                                                                    __html:
                                                                        list.content,
                                                                }}
                                                            />
                                                        </ul>
                                                    </React.Fragment>
                                                ))}
                                            </div>
                                        </div>
                                        <div className="d-block text-center"></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

export default Job;
export const pageQuery = graphql`
    query PostPage($id: String!) {
        # Select the post which equals this id.
        lever(id: { eq: $id }) {
            id
            additional
            applyUrl
            categories {
                commitment
                department
                level
                location
                team
            }
            description
            descriptionPlain
            text
            lists {
                content
                text
            }
        }
    }
`;
